import Prompt from '../../../components/zz-prompt/index.vue'

export default {
	components: {
		Prompt
	},
	data() {
		return {
			uuid: '', //UUID
			disData: [], //派单详情数据
			refuseReason: '', //拒绝原因
			promptVisible: true, //弹出层
			butState: false, //操作按钮
		}
	},
	onLoad: function(option) {
		this.uuid = option.id
		this.dispatchDetailsQuery()
	},
	methods: {
		cancel() {
			//弹出框取消按钮
			this.promptVisible = true
		},
		clickPromptConfirm(val) {
			//拒绝派工单
			let data = {
				dispatchDetailBeanList: [this.disData],
				receiveState: 2,
				refuseReason: val
			}
			let that = this
			if (val != '') {
				this.$http.post(this.$store.state.saveState, data).then(res => {
					if (res.data.code == 200) {
						uni.showModal({
							title: '操作成功！',
							showCancel: false,
							success() {
								that.promptVisible = true
								that.dispatchDetailsQuery()
							}
						});
						this.butState = true
					}
				})
			} else {
				uni.showModal({
					title: '请输入拒绝原因！',
					showCancel: false
				});
			}


			console.log(data)
		},
		Refused() {
			//拒绝按钮

			if (this.disData.receiveState !== 0) {
				uni.showModal({
					title: '提示',
					content: '已经操作过的订单，无法再次操作！',
					showCancel: false
				});

			} else {
				this.promptVisible = false
			}



		},
		accept() {
			if (this.disData.receiveState !== 0) {
				uni.showModal({
					title: '提示',
					content: '已经操作过的订单，无法再次操作！',
					showCancel: false
				});

			} else {
				let that = this
				//接受按钮
				let data = {
					dispatchDetailBeanList: [this.disData],
					receiveState: 1,

				}

				uni.showModal({
					title: '操作',
					content: '确认接受该条派工单？',
					showCancel: true,
					success(res) {
						if (res.confirm) {
							that.$http.post(that.$store.state.saveState, data).then(res => {
								if (res.data.code == 200) {
									that.butState = true
									that.dispatchDetailsQuery()
								}
							})
						}
					}

				});
			}




		},

		dispatchDetailsQuery() {
			//查询派工单详情
			this.$http.get(this.$store.state.dispatchDetailsbyID, {
				uuid: this.uuid
			}).then(res => {
				console.log(res)
				this.disData = res.data.data
				if (res.data.data.receiveState !== 0) {
					this.butState = true
				}
			})
		}
	},

}
